import React, { useState } from 'react';
import styles from'./SessionDetails.module.css';
import { SessionResults } from './Constants';
import { VictoryChart, VictoryLine, VictoryTheme } from 'victory';

interface Props
{
    user: any;
    session: SessionResults;
}

const Subjects: React.FunctionComponent<Props> = (p: Props) =>
{
    const { session } = p;
    const [ results ] = useState(() => p.session.resultData.map(result => ({ x: result.time, y: result.leftValue})));

    let chart = results.slice(0, 500);

    return (
        <div className={styles.sessionDetails}>
            <div className={styles.personDetails}>
                <div className={styles.name}>{session.name}</div>
                <div className={styles.rank}>{session.rank}</div>
            </div>
            <div className={styles.chartTitle}>{`${session.platformName} ${session.dateString}`}</div>
            <div className={styles.chart}>
                <VictoryChart
                    animate={{
                        duration: 500,
                        onLoad: { duration: 100 }
                    }}
                >
                    <VictoryLine
                        data={chart}
                    />
                </VictoryChart>
                </div>
        </div>
    );
}

export default Subjects;